import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { getcustomerList } from "../../actions/customerAction";
import { getPiutangCustomer } from "../../actions/TrasferCustomerAction";
import { AxiosMasterGet, AxiosMasterPost } from "../../Axios";
import { renderFieldSelect } from "../../renderField";
import { finishSend, progressSend } from "../../actions/LogicAction";
import { berhasil, gagal, warning } from "../../Alert";
import TransferBalance from "./TransferBalance";
import ModalMasterData from "../../DataMaster/Component/ModalMasterData";
import { hideModal, showModal } from "../../actions/ModalAction";
import { getUUID } from "../../actions/fakturAction";
import { getToday } from "../../helper";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormTransferBalance extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "kode_customer",
          text: "Kode Customer",
        },
        {
          dataField: "piutang_gr",
          text: "Piutang GR",
        },
        {
          dataField: "piutang_rp",
          text: "Piutang RP",
        },
        {
          dataField: "titip_gr",
          text: "Titip GR",
        },
        {
          dataField: "titip_rp",
          text: "Titip RP",
        },
      ],
      searchText: "",
      listCustomer: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
  }
  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  onSubmitToko(data) {
    var split = data.split("|");
    this.props.change("kodeToko", split[0]);
    this.props.change("kodeCustomer", split[1]);
    this.props.dispatch(getPiutangCustomer(split[0], this.props.tanggal));
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `trx-transfer-balance/get-piutang-titipan/${split[0]}&${this.props.tanggal}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        this.props.change("piutang_gr", res.data.piutang_gr);
        this.props.change("piutang_rp", res.data.piutang_rp);
        this.props.change("titip_gr", res.data.titip_gr);
        this.props.change("titip_rp", res.data.titip_rp);
      })
      .catch((err) =>
        gagal(`Gagal Ambil Data , Detial : ${err}`).then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }

  handleSubmit(data) {
    if (data.kode_customer_tujuan === undefined) {
      warning("Mohon Isi Customer Tujuan");
      return false;
    }
    let simpan = {
      no_transfer: this.props.noFaktur,
      tanggal: this.props.tanggal || getToday(),
      kode_customer_from: data.kode_customer,
      kode_customer_to: data.kode_customer_tujuan,
      total_jml_gr: data.piutang_gr_ambil,
      total_jml_rp: data.piutang_rp_ambil,
      total_titip_gr: data.titip_gr_ambil,
      total_titip_rp: data.titip_rp_ambil,
    };
    AxiosMasterPost("trx-transfer-balance/simpan", simpan)
      .then(() => berhasil(`Transfer Berhasil`))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => this.props.dispatch(hideModal()))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => window.location.reload())
      .catch((err) => gagal(`Gagal Transfer, Detail : ${err}`));
  }

  render() {
    return (
      <div className="row">
        <div className="col-md-4 col-lg-4">
          <Field
            name="tanggal"
            label="Tanggal"
            type="date"
            component={renderField}
          />
        </div>
        <div className="col-md-4 col-lg-4 d-none">
          <Field
            name="kodeToko"
            component={renderField}
            type="text"
            label="Nama Toko"
          />
        </div>

        <div className="col-lg-4">
          <Field
            name="kode_customer"
            component={renderFieldSelect}
            options={this.props.customer.map((obj) => {
              var payload = {
                value: obj.kode_customer + "|" + obj.nama_customer,
                name: obj.nama_toko,
              };
              return payload;
            })}
            onChange={(data) => this.onSubmitToko(data)}
            type="text"
            label="Nama Toko"
            placeholder="Masukan Nama Toko"
          />
        </div>
        <div className="col-md-4 col-lg-4">
          <Field
            name="kodeCustomer"
            component={renderField}
            type="text"
            label="Nama Customer"
            readOnly={true}
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="piutang_gr"
            component={renderField}
            type="text"
            label="Piutang GR"
            placeholder="Masukan Piutang GR"
            readOnly
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="piutang_rp"
            component={renderField}
            type="text"
            label="Piutang RP"
            placeholder="Masukan Piutang RP"
            readOnly
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="titip_rp"
            component={renderField}
            type="text"
            label="Titip RP"
            placeholder="Masukan Titip RP"
            readOnly
          />
        </div>
        <div className="col-lg-3">
          <Field
            name="titip_gr"
            component={renderField}
            type="text"
            label="Titip GR"
            placeholder="Masukan Titip GR"
            readOnly
          />
        </div>
        <div className="col-lg-12 col-md-12 mt-2">
          <div className="text-right">
            <button
              className="btn btn-primary"
              type="button"
              disabled={this.props.onSend}
              onClick={() => this.props.dispatch(showModal())}
            >
              {this.props.onSend ? (
                <span
                  className="spinner-border spinner-border-sm mr-3"
                  aria-hidden="true"
                ></span>
              ) : null}
              Transfer Balance
            </button>
          </div>
        </div>
        <ModalMasterData
          content={
            <TransferBalance onSubmit={(data) => this.handleSubmit(data)} />
          }
          title="Transfer Balance"
        />
      </div>
    );
  }
}

FormTransferBalance = reduxForm({
  form: "FormTransferBalance",
  enableReinitialize: true,
})(FormTransferBalance);
const selector = formValueSelector("FormTransferBalance"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, tanggal } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "tanggal"
  );
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    customer: state.provinsi.customer,
    piutang_gr: state.provinsi.listPiutangCustomer.piutang_gr,
    piutang_rp: state.provinsi.listPiutangCustomer.piutang_rp,
    titip_gr: state.provinsi.listPiutangCustomer.titip_gr,
    titip_rp: state.provinsi.listPiutangCustomer.titip_rp,
    tanggal: tanggal,
    onSend: state.provinsi.onSend,
    noFaktur: state.provinsi.noFaktur,
  };
})(FormTransferBalance);
