import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { getcustomerList } from "../../actions/customerAction";
import { renderField, renderFieldSelect } from "../../renderField";

class TransferBalance extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.props.dispatch(getcustomerList());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-6">
            <Field
              name="kode_customer"
              component={renderFieldSelect}
              options={this.props.customer.map((obj) => {
                var payload = {
                  value: obj.kode_customer,
                  name: obj.nama_customer,
                };
                return payload;
              })}
              type="text"
              label="Nama Toko"
              placeholder="Masukan Nama Toko"
              disabled
            />
          </div>
          <div className="col-lg-6">
            <Field
              name="kode_customer_tujuan"
              component={renderFieldSelect}
              options={this.props.customer.map((obj) => {
                var payload = {
                  value: obj.kode_customer,
                  name: obj.nama_customer,
                };
                return payload;
              })}
              type="text"
              label="Nama Toko Tujuan "
              placeholder="Masukan Nama Toko Tujuan"
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="piutang_gr"
              component={renderField}
              type="text"
              label="Piutang GR"
              placeholder="Masukan Piutang GR"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="piutang_rp"
              component={renderField}
              type="text"
              label="Piutang RP"
              placeholder="Masukan Piutang RP"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="titip_gr"
              component={renderField}
              type="text"
              label="Titip GR"
              placeholder="Masukan Titip GR"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="titip_rp"
              component={renderField}
              type="text"
              label="Titip RP"
              placeholder="Masukan Titip RP"
              readOnly
            />
          </div>

          <div className="col-lg-3">
            <Field
              name="piutang_gr_ambil"
              component={renderField}
              type="number"
              label="Ambil Piutang GR"
              placeholder="Masukan Piutang GR"
              onChange={() =>
                this.props.change(
                  "piutang_gr_akhir",
                  this.props.piutang_gr_akhir
                )
              }
              onBlur={() =>
                this.props.change(
                  "piutang_gr_akhir",
                  this.props.piutang_gr_akhir
                )
              }
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="piutang_rp_ambil"
              component={renderField}
              type="number"
              label="Ambil Piutang RP"
              placeholder="Masukan Piutang RP"
              onChange={() =>
                this.props.change(
                  "piutang_rp_akhir",
                  this.props.piutang_rp_akhir
                )
              }
              onBlur={() =>
                this.props.change(
                  "piutang_rp_akhir",
                  this.props.piutang_rp_akhir
                )
              }
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="titip_gr_ambil"
              component={renderField}
              type="number"
              label="Ambil Titip GR"
              placeholder="Masukan Titip GR"
              onChange={() =>
                this.props.change("titip_gr_akhir", this.props.titip_gr_akhir)
              }
              onBlur={() =>
                this.props.change("titip_gr_akhir", this.props.titip_gr_akhir)
              }
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="titip_rp_ambil"
              component={renderField}
              type="number"
              label="Ambil Titip RP"
              placeholder="Masukan Titip RP"
              onChange={() =>
                this.props.change("titip_rp_akhir", this.props.titip_gr_akhir)
              }
              onBlur={() =>
                this.props.change("titip_rp_akhir", this.props.titip_gr_akhir)
              }
            />
          </div>

          <div className="col-lg-12">
            <div className="row">
              <div className="col-lg-11">
                <hr />
              </div>
              <div className="col-lg-1">
                <i className="fa fa-minus"></i>
              </div>
            </div>
          </div>
          <div className="col-lg-3">
            <Field
              name="piutang_gr_akhir"
              component={renderField}
              type="text"
              label="Piutang GR"
              placeholder="Masukan Piutang GR"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="piutang_rp_akhir"
              component={renderField}
              type="text"
              label="Piutang RP"
              placeholder="Masukan Piutang RP"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="titip_gr_akhir"
              component={renderField}
              type="text"
              label="Titip GR"
              placeholder="Masukan Titip GR"
              readOnly
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="titip_rp_akhir"
              component={renderField}
              type="text"
              label="Titip RP"
              placeholder="Masukan Titip RP"
              readOnly
            />
          </div>
        </div>
        <div className="col-lg-12 col-md-12 mt-2">
          <div className="text-right">
            <button
              className="btn btn-primary"
              type="submit"
              disabled={this.props.onSend}
            >
              {this.props.onSend ? (
                <span
                  className="spinner-border spinner-border-sm mr-3"
                  aria-hidden="true"
                ></span>
              ) : null}
              Transfer Balance
            </button>
          </div>
        </div>
      </form>
    );
  }
}

TransferBalance = reduxForm({
  form: "TransferBalance",
  enableReinitialize: true,
})(TransferBalance);
const selector = formValueSelector("TransferBalance");
export default connect((state) => {
  const {
    piutang_gr,
    piutang_rp,
    titip_gr,
    titip_rp,
    piutang_gr_ambil,
    piutang_rp_ambil,
    titip_gr_ambil,
    titip_rp_ambil,
  } = selector(
    state,
    "piutang_gr",
    "piutang_rp",
    "titip_gr",
    "titip_rp",
    "piutang_gr_ambil",
    "piutang_rp_ambil",
    "titip_gr_ambil",
    "titip_rp_ambil"
  );
  return {
    customer: state.provinsi.customer,
    titip_rp_akhir: parseFloat(titip_rp) - parseFloat(titip_rp_ambil || 0),
    titip_gr_akhir: parseFloat(titip_gr) - parseFloat(titip_gr_ambil || 0),
    piutang_rp_akhir:
      parseFloat(piutang_rp) - parseFloat(piutang_rp_ambil || 0),
    piutang_gr_akhir:
      parseFloat(piutang_gr) - parseFloat(piutang_gr_ambil || 0),
    initialValues: {
      piutang_gr: state.provinsi.listPiutangCustomer.piutang_gr,
      piutang_rp: state.provinsi.listPiutangCustomer.piutang_rp,
      titip_gr: state.provinsi.listPiutangCustomer.titip_gr,
      titip_rp: state.provinsi.listPiutangCustomer.titip_rp,
      kode_customer: state.provinsi.listPiutangCustomer.kode_customer,
    },
  };
})(TransferBalance);
